import {
	mapState
} from 'vuex';
var dataCheck;
export default {
	data() {
		return {
			source: 0,
			addressList: [

			]
		}
	},
	onLoad(option) {
		uni.request({
			url: this.api + '/api/userContactList',
			data: {
				token: this.userInfo.token,
				pageNo: '1',
				pageSize: '10',
			},
			success: res => {
				var data = res.data.list;
				console.log(data)
				this.addressList = data
			}
		});
	},
	computed: {
		...mapState(['userInfo'])
	},
	methods: {
		//选择地址
		checkAddress(item) {
			if (this.source == 1) {
				//this.$api.prePage()获取上一页实例，在App.vue定义
				this.$api.prePage().addressData = item;
				uni.navigateBack()
			}
		},
		addAddress() {
			uni.navigateTo({
				url: `/pages/address/addressManage`
			})
		},
		editAddress(item) {
			uni.navigateTo({
				url: `/pages/address/addressEdit?data=${JSON.stringify(item)}`
			})
		},
		deleteAddress(item) {
			console.log(item)
			uni.showModal({	
				title: '温馨提示',
				content: '请确认是否删除该联系人',
				success: (res) =>{
					if (res.confirm) {
						// dataCheck = true
						var user = this.userInfo
						var param = {
							token: user.token,
							ids: '' + item.id,
						}
						uni.request({
							url: this.api + '/api/delUserContact',
							data: param,
							success: (res1) => {
								console.log(res1)
								if (res1.data.code == 1) {
									setTimeout(() => {
										this.$api.msg('删除成功');
										uni.navigateBack()
									}, 1600)
								}
							},
						});
						// this.deleteAdd(item);
					} else if (res.cancel) {
						this.$api.msg('取消');
						return;
					}
				}
			});
		},
		//添加或修改成功之后回调
		refreshList(data, type) {
			//添加或修改后事件，这里直接在最前面添加了一条数据，实际应用中直接刷新地址列表即可
			this.addressList.unshift(data);
			console.log(data, type);
		},
		
	}
}
